@font-face {
  font-family: 'icon';
  src:
    url('asset/font/icon.ttf?zch8ap') format('truetype'),
    url('asset/font/icon.woff?zch8ap') format('woff'),
    url('asset/font/icon.svg?zch8ap#icon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

